.todo{
	background-color: #112c35;
	display: block;
	margin-left: auto;
	margin-right: auto;
	max-width: 500px;
	border-radius: 10px;
	box-shadow: 0 0 20px rgba(0,0,0,0.5);}

.title{
	text-align: center;
	padding-top: 30px;
	color: #45badd;
}

.todo input{
	margin: 20px;
	width: 350px;
	height: 30px;
	border-radius: 10px;
	font-size: 15px;
	outline: none;
	padding-left: 10px;
}

.btn{
	color: #45badd;
	width: 70px;
	height: 25px;
	background: none;
	border: 2px solid #45badd;
	border-radius:5px;
	outline: none;
	cursor: pointer;
}

.btn:hover{
	transform: scale(1.07);
	transition: 0.2s;
}

ul{
	padding-left: 0;
}

li{
	color: #45badd;
	padding: 20px;
	margin-left: 0;
	display: flex;
	justify-content: space-between;
}

li p{
	margin: 0;
}

li button{
	text-align: center;
}